import { Controller } from '@ali/4ever-cangjie';
import { SetStickerVisibleProps } from '../types';

export default function setStickerPanelVisible(
  controller: Controller,
  payload: SetStickerVisibleProps,
) {
  const { visible = false } = payload;
  const { data } = controller.value;

  if (data._stickerPanel === visible) {
    return controller;
  }

  return controller.setData({
    ...controller.value.data,
    _stickerPanel: visible,
  });
}
